
.profile {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}
.profile .profile-wrapper {
    width: 90%;
    height: 100%;
    margin: 0 auto;
}
.profile .profile-wrapper .user-info {
      text-align: center;
}
.profile .profile-wrapper .user-info img {
        width: 45%;
        margin: 10px 0;
        border: 5px solid #fff;
        border-radius: 50%;
}
.profile .profile-wrapper .user-info .msn {
        margin: 20px 0;
        font-size: 18px;
        color: #193c6b;
}
.profile .profile-wrapper .user-info .check {
        width: 80%;
        height: 50px;
        margin: 0 auto;
        border-radius: 30px;
        font-size: 20px;
        color: #fff;
        background: #ff3334;
        outline: none;
}
.profile .profile-wrapper .soogs {
      margin-top: 20px;
      height: 36%;
      padding-bottom: 50px;
}
.profile .profile-wrapper .soogs .title {
        color: #0f2443;
        font-size: 20px;
        text-align: center;
        text-transform: uppercase;
        padding-bottom: 20px;
        border-bottom: 1px solid #12203e;
}
.profile .profile-wrapper .soogs .txt-loading {
        color: #ff4660;
        font-size: 20px;
        text-align: center;
        margin-top: 50px;
}
.profile .profile-wrapper .soogs .list li {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        border-bottom: 1px solid #12203e;
        color: #ff3334;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        padding: 12px 0;
}
.profile .profile-wrapper .soogs .list li.playing .serial-number {
          color: #05c3ff;
}
.profile .profile-wrapper .soogs .list li.playing .song-info .singer {
          color: #4f9aff;
}
.profile .profile-wrapper .soogs .list li.playing .song-info .song-name {
          color: #00ffc6;
}
.profile .profile-wrapper .soogs .list li.playing .time-total {
          color: #3cfb00;
}
.profile .profile-wrapper .soogs .list li .serial-number {
          -webkit-box-flex: 0;
              -ms-flex: 0 0 20px;
                  flex: 0 0 20px;
          padding-right: 10px;
          color: #17345b;
          font-size: 18px;
}
.profile .profile-wrapper .soogs .list li .song-info {
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
          padding-right: 20px;
          overflow: hidden;
          text-overflow: ellipsis;
          word-break: normal;
}
.profile .profile-wrapper .soogs .list li .song-info .song-name {
            color: #2a5ea4;
            width: 100%;
            height: 18px;
            overflow: hidden;
            text-overflow: ellipsis;
            word-break: normal;
}
.profile .profile-wrapper .soogs .list li .song-info .singer {
            color: #ffffff;
            width: 100%;
            height: 18px;
            overflow: hidden;
            text-overflow: ellipsis;
            word-break: normal;
}
.profile .profile-wrapper .soogs .list li .icon {
          -webkit-box-flex: 0;
              -ms-flex: 0 0 20px;
                  flex: 0 0 20px;
          font-size: 24px;
}
.profile .profile-wrapper .more {
      margin-top: -40px;
      height: 40px;
      text-align: center;
}
.profile .profile-wrapper .more input {
        width: 60%;
        height: 40px;
        border: 2px solid #183b69;
        border-radius: 20px;
        background: transparent;
        color: #fff;
        outline: none;
}



