
.signin {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("signin-bg.png") no-repeat;
  background-size: cover;
}
.signin .logo-bg {
    width: 100%;
    text-align: center;
}
.signin .logo-bg img {
      width: 300px;
      height: 300px;
}
.signin .sign {
    width: 100%;
}
.signin .sign .input {
      width: 90%;
      margin: 0 auto;
      text-align: left;
}
.signin .sign .input input {
        width: 100%;
        height: 50px;
        line-height: 50px;
        color: #fff;
        border-radius: 25px;
        text-indent: 20px;
        font-size: 20px;
        outline: none;
}
.signin .sign .input-text {
      margin-bottom: 15px;
}
.signin .sign .input-text p {
        margin-left: 20px;
        margin-bottom: 10px;
        font-size: 16px;
        text-transform: uppercase;
        color: #17355e;
}
.signin .sign .input-text input {
        background: rgba(0, 109, 255, 0.15);
}
.signin .sign .input-btn {
      margin-top: 40px;
}
.signin .sign .input-btn input {
        background: #ff3334;
}
.signin .sign .forgot {
      width: 90%;
      font-size: 0;
      margin: 0 auto;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      margin-top: 50px;
      text-transform: uppercase;
      text-align: center;
}
.signin .sign .forgot .line {
        position: relative;
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        width: 100%;
        height: 1px;
        display: inline-block;
        background: #203364;
}
.signin .sign .forgot a {
        position: relative;
        z-index: 3;
        padding: 0 20px;
        font-size: 18px;
        color: #183b69;
}



