
#app {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: url("./background.png") no-repeat;
  background-size: cover;
}
#app .bg {
    width: 100%;
}
#app .init-enter-active, #app .init-leave-active {
    transition: all 0.8s linear;
}
#app .init-enter, #app .init-leave-active {
    opacity: 0;
}
#app .moveup-enter-active, #app .moveup-leave-active {
    transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
#app .moveup-enter, #app .moveup-leave-active {
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
}



