
.menu {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 110;
}
.menu .menu-bg {
    width: 100%;
    height: 100%;
    background: url("./menu-bg.png") no-repeat;
    background-size: cover;
    opacity: 0.98;
}
.menu .menu-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 102;
}
.menu .menu-container .menu-container-wrapper {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      width: 82%;
      height: 100%;
      margin: 0 auto;
}
.menu .menu-container .menu-container-wrapper .user {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        margin-top: 20px;
        font-size: 0;
}
.menu .menu-container .menu-container-wrapper .user .photo {
          width: 10%;
