          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
}
.menu .menu-container .menu-container-wrapper .menu-categories ul li {
            margin-bottom: 50px;
            color: #fff;
}
.menu .menu-container .menu-container-wrapper .menu-categories ul li i {
              margin-right: 30px;
}
.menu .menu-container .menu-container-wrapper .menu-categories .out {
          -webkit-box-flex: 0;
