
.navigation {
  position: absolute;
  bottom: 0;
  right: 0;
}
.navigation.active {
    width: 100%;
    height: 100%;
}
.navigation .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.7);
    transition: all 0.3s;
}
.navigation .overlay.fade-enter-active, .navigation .overlay.fade-leave-active {
      transition: all 0.3s;
}
.navigation .overlay.fade-enter, .navigation .overlay.fade-leave-active {
      opacity: 0;
}
.navigation .overlay.active {
      display: block;
      opacity: 1;
      transition: all 0.3s;
}
.navigation .icon {
    width: 50px;
    height: 50px;
    border-top-left-radius: 50px;
    position: absolute;
    right: 0;
    bottom: 0;
    background: #231433;
    color: #fff;
    line-height: 3.8;
    text-align: center;
    text-indent: 10px;
    z-index: 100;
    opacity: 0.5;
    transition: all 0.3s;
}
.navigation .icon.active {
      opacity: 1;
      transition: all 0.3s;
}
.navigation .fan-menu {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 150px;
    height: 150px;
    overflow: hidden;
    -webkit-transform: scale(0);
            transform: scale(0);
    transition: all 0.3s ease;
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
    border-top-left-radius: 150px;
    transition: all 0.3s;
}
.navigation .fan-menu.active {
      width: 150px;
      height: 150px;
      overflow: hidden;
      border-top-left-radius: 150px;
      -webkit-transform: scale(1);
              transform: scale(1);
      transition: all 0.3s;
}
.navigation .fan-menu ul li {
      position: absolute;
      right: 0;
      bottom: 0;
      width: 150px;
      height: 150px;
      -webkit-transform-origin: 100% 100%;
              transform-origin: 100% 100%;
      overflow: hidden;
}
.navigation .fan-menu ul li:nth-child(1) {
        background-color: #f44e4e;
        -webkit-transform: rotate(0) skew(60deg);
                transform: rotate(0) skew(60deg);
}
.navigation .fan-menu ul li:nth-child(2) {
        background-color: #f45757;
        -webkit-transform: rotate(30deg) skew(60deg);
                transform: rotate(30deg) skew(60deg);
}
.navigation .fan-menu ul li:nth-child(3) {
        background-color: #f44e4e;
        -webkit-transform: rotate(60deg) skew(60deg);
                transform: rotate(60deg) skew(60deg);
}
.navigation .fan-menu ul li a {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 300px;
        height: 300px;
        padding-top: 18px;
        font-size: 20px;
        -webkit-transform: skew(-60deg) rotate(-73deg) scale(1);
                transform: skew(-60deg) rotate(-73deg) scale(1);
        text-align: center;
        color: #fff;
}



