
.my-music {
  width: 100%;
  height: 100%;
  position: relative;
}
.my-music .my-music-wrapper {
    width: 90%;
    height: 100%;
    margin: 0 auto;
}
.my-music .my-music-wrapper .songs {
      height: 85%;
      width: 90%;
      position: relative;
      overflow: hidden;
      margin: 0 auto;
}
.my-music .my-music-wrapper .songs .songs-wrapper {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
}
.my-music .my-music-wrapper .songs .songs-wrapper .list li:last-child .song-item {
          border: 0;
}
.my-music .my-music-wrapper .songs .songs-wrapper .list li .song-item {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          border-bottom: 1px solid #314671;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
          padding: 15px 0;
}
