                    flex: 0 0 20px;
            padding-right: 10px;
            color: #49618e;
            font-size: 18px;
}
.my-music .my-music-wrapper .songs .songs-wrapper .list li .song-item .song-info {
            -webkit-box-flex: 1;
                -ms-flex: 1;
                    flex: 1;
            padding-right: 20px;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
                -ms-flex-direction: column;
                    flex-direction: column;
            overflow: hidden;
}
.my-music .my-music-wrapper .songs .songs-wrapper .list li .song-item .song-info .singer {
              -webkit-box-flex: 1;
                  -ms-flex: 1 1 auto;
                      flex: 1 1 auto;
              width: 100%;
              height: 18px;
              color: #2a5ea4;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
}
.my-music .my-music-wrapper .songs .songs-wrapper .list li .song-item .song-info .song-name {
              -webkit-box-flex: 1;
                  -ms-flex: 1 1 auto;
                      flex: 1 1 auto;
              height: 18px;
              color: #ffffff;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
}
.my-music .my-music-wrapper .songs .songs-wrapper .list li .song-item .icon {
            font-size: 24px;
            color: #ff3334;
}
.my-music .my-music-wrapper .songs .songs-wrapper .txt-loading {
          font-size: 22px;
          text-align: center;
          margin-top: 100px;
          color: #ff3334;
}
.my-music .music-control {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    width: 100%;
    height: 50px;
    bottom: 0;
}
.my-music .music-control .pic-control {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 66px;
              flex: 0 0 66px;
}
