}
.player .player-wrapper .player-top .song .player-bg {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 60%;
          background-size: cover;
          background-repeat: no-repeat;
          background-position: center center;
          -webkit-filter: blur(20px);
                  filter: blur(20px);
          opacity: 0.5;
