.player .player-wrapper .player-top .song .song-pic .loading {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            width: 100%;
            height: 100%;
            font-size: 50px;
            color: #fff;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
            text-align: center;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon {
              width: 100px;
              height: 100px;
              position: relative;
              margin: 0 auto;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon span {
                display: inline-block;
                width: 16px;
                height: 16px;
                border-radius: 50%;
                background: lightgreen;
