}
.player .player-wrapper .player-top .song .song-pic .loading .icon span:nth-child(2) {
                  left: 14px;
                  top: 14px;
                  -webkit-animation-delay: 0.26s;
                          animation-delay: 0.26s;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon span:nth-child(3) {
                  left: 50%;
