}
.player .player-wrapper .player-top .song .song-pic .loading .icon span:nth-child(6) {
                  right: 14px;
                  bottom: 14px;
                  -webkit-animation-delay: 0.78s;
                          animation-delay: 0.78s;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon span:nth-child(7) {
                  bottom: 0;
                  left: 50%;
                  margin-left: -8px;
                  -webkit-animation-delay: 0.91s;
                          animation-delay: 0.91s;
}
.player .player-wrapper .player-top .song .song-pic .loading .icon span:nth-child(8) {
                  bottom: 14px;
                  left: 14px;
                  -webkit-animation-delay: 1.04s;
                          animation-delay: 1.04s;
