
.playlist {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: url(../../assets/Background.png) no-repeat;
  background-size: cover;
  overflow: hidden;
}
.playlist .playlist-wrapper {
    width: 100%;
    height: 100%;
}
.playlist .playlist-wrapper .playlist-header-wrapper {
      width: 100%;
      background: #061326;
}
.playlist .playlist-wrapper .playlist-header-wrapper .playlist-header {
        width: 90%;
        margin: 0 auto;
}
.playlist .playlist-wrapper .playlist-header-wrapper .playlist-header .play-singer {
          padding: 0 35px;
          font-size: 16px;
          font-weight: bold;
          text-align: center;
          text-transform: uppercase;
          color: #183965;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
}
.playlist .playlist-wrapper .playlist-header-wrapper .play {
        width: 100%;
}
.playlist .playlist-wrapper .playlist-header-wrapper .play .play-function {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          width: 90%;
          padding: 30px 0;
          margin: 0 auto;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
}
.playlist .playlist-wrapper .playlist-header-wrapper .play .play-function .time {
            -webkit-box-flex: 0;
                -ms-flex: 0 0 30px;
                    flex: 0 0 30px;
            color: #fff;
}
.playlist .playlist-wrapper .playlist-header-wrapper .play .play-function .function {
            -webkit-box-flex: 1;
                -ms-flex: 1;
                    flex: 1;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            padding: 0 5px;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
}
.playlist .playlist-wrapper .playlist-header-wrapper .play .play-function .function .item {
              -webkit-box-flex: 1;
                  -ms-flex: 1;
                      flex: 1;
              text-align: center;
              color: #0d3f84;
}
.playlist .playlist-wrapper .playlist-header-wrapper .play .play-function .function .player-btn {
              color: #ff3334;
}
.playlist .playlist-wrapper .playlist-header-wrapper .play .play-function .icon {
            font-size: 36px;
}
.playlist .playlist-wrapper .playlist-header-wrapper .play .play-function .cycle-mode .icon,
          .playlist .playlist-wrapper .playlist-header-wrapper .play .play-function .repeat .icon {
            font-size: 22px;
}
.playlist .playlist-wrapper .playlist-header-wrapper .play .play-bar {
          position: relative;
          width: 100%;
          height: 5px;
          background: #052045;
}
.playlist .playlist-wrapper .playlist-header-wrapper .play .play-bar .play-now {
            position: absolute;
            top: 0;
            left: 0;
            height: 5px;
            background: #ff3334;
            transition: all 0.5s;
}
.playlist .playlist-wrapper .songs {
      height: 100%;
      width: 90%;
      position: relative;
      overflow: hidden;
      margin: 0 auto;
}
.playlist .playlist-wrapper .songs .songs-wrapper {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
}
.playlist .playlist-wrapper .songs .songs-wrapper .list {
          padding-bottom: 150px;
}
.playlist .playlist-wrapper .songs .songs-wrapper .list li .song-item {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            border-bottom: 1px solid #12203e;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
            padding: 15px 0;
}
.playlist .playlist-wrapper .songs .songs-wrapper .list li .song-item.playing .serial-number {
              color: #05c3ff;
}
.playlist .playlist-wrapper .songs .songs-wrapper .list li .song-item.playing .song-info .singer {
              color: #4f9aff;
}
.playlist .playlist-wrapper .songs .songs-wrapper .list li .song-item.playing .song-info .song-name {
              color: #00ffc6;
}
.playlist .playlist-wrapper .songs .songs-wrapper .list li .song-item.playing .time-total {
              color: #3cfb00;
}
.playlist .playlist-wrapper .songs .songs-wrapper .list li .song-item .serial-number {
              -webkit-box-flex: 0;
                  -ms-flex: 0 0 20px;
                      flex: 0 0 20px;
              padding-right: 10px;
              color: #17345b;
              font-size: 18px;
}
.playlist .playlist-wrapper .songs .songs-wrapper .list li .song-item .song-info {
              -webkit-box-flex: 1;
                  -ms-flex: 1;
                      flex: 1;
              padding-right: 20px;
              display: -webkit-box;
              display: -ms-flexbox;
              display: flex;
              -webkit-box-orient: vertical;
              -webkit-box-direction: normal;
                  -ms-flex-direction: column;
                      flex-direction: column;
              overflow: hidden;
}
.playlist .playlist-wrapper .songs .songs-wrapper .list li .song-item .song-info .singer {
                -webkit-box-flex: 1;
                    -ms-flex: 1 1 auto;
                        flex: 1 1 auto;
                width: 100%;
                height: 18px;
                color: #2a5ea4;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
}
.playlist .playlist-wrapper .songs .songs-wrapper .list li .song-item .song-info .song-name {
                -webkit-box-flex: 1;
                    -ms-flex: 1 1 auto;
                        flex: 1 1 auto;
                height: 18px;
                color: #ffffff;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
}
.playlist .playlist-wrapper .songs .songs-wrapper .list li .song-item .time-total {
              color: #fff;
}
.playlist .playlist-wrapper .songs .songs-wrapper .txt-loading {
          font-size: 30px;
          text-align: center;
          margin-top: 100px;
          color: #ff3334;
}



